import axios from 'axios'; 
import jsonAdapter from 'axios-jsonp';
import { post, deleteCall, get } from './BaseApi';

export const GetPumpReading = value => dispatch =>{
   
    let url="http://pumpmonitorapi-dev.us-east-1.elasticbeanstalk.com/api/PumpMonitor/GetPumpReading?imei=";
   return get(url+value)
       .then(res => dispatch({ type: "GET_READING", payload: res.data }))
       .catch(err => console.log("Error from Get motor reading", err))
  
}

