
import React, { useEffect } from "react"; 
import { connect } from "react-redux"; 
import LoginComponent from "../../components/Login"; 
import {setHeaderStatus,setLoginDataHeader,setFooterStatus} from "../../actions/Header"; 
 import {GetPumpReading} from "../../actions/Dashboard";
import  {login} from "../../actions/Register";


const Login = ({ setHeaderStatus,setLoginDataHeader,setFooterStatus,GetPumpReading,login }) => { 

  useEffect(() => { 
    setHeaderStatus(true); 
      setFooterStatus(true); 
  }, []); 
  return <LoginComponent setLoginDataHeader={setLoginDataHeader} GetPumpReading={GetPumpReading} login={login}/>; 
}; 
export default connect(null,{setFooterStatus, setHeaderStatus,setLoginDataHeader,GetPumpReading,login})(Login); 
