import React from "react";
export const Tick = ({ tick, count }) => (
<div>
<div
style={{
position: "absolute",
marginTop: 14,
width: 1,
height: 20,
backgroundColor: "#669999",
left: `${tick.percent}%`
}}
/>
<div
style={{
position: "absolute",
marginTop: 42,
fontSize: 12,
textAlign: "center",
marginLeft: `${-(100 / count) / 2}%`,
width: `${100 / count}%`,
left: `${tick.percent}%`
}}
>
{tick.value}
</div>
</div>
);
