import axios from 'axios'; 
import { post, deleteCall, get } from './BaseApi';

export const registration = userDetails => dispatch => { 
    console.log("user details",userDetails);
	return post("http://pumpmonitorapi-dev.us-east-1.elasticbeanstalk.com/api/PumpMonitor/Register",userDetails )
		.then(res => dispatch({ type: "REGISTRATION", payload:res.data}))
		.catch(err => console.log("Error from Registration", err))
};

export const login = userDetails => dispatch => { 
	return post("http://pumpmonitorapi-dev.us-east-1.elasticbeanstalk.com/api/PumpMonitor/Authenticate",userDetails )
		.then(res => dispatch({ type: "LOGIN", payload: res.data}))
		.catch(err => console.log("Error from Login ", err))
};
