export default (state = {motorData:{},motorReaddingData:{}},action) => { 

    console.log(action.payload); 
  switch (action.type) {
    case "SET_MOTER_DATA": 
      return { ...state, motorData: action.payload };  
           case "GET_READING": 
      return { ...state, motorReaddingData: action.payload };  
    default: 
      return state; 

  } 

}; 