export default (state = {showHeader: true,loginData: "",showFooter:true},action) => { 

    console.log(action.payload); 

  switch (action.type) { 

    case "SET_HEADER_STATUS": 

      return { ...state, showHeader: action.payload }; 

      case "SET_LOGIN_DATA_HEADER": 

          return{...state, loginData:action.payload } 

    case "SET_FOOTER_STATUS": 

      return { ...state, showFooter: action.payload }; 

    default: 

      return state; 

  } 

}; 
