import React, { useState } from "react"; 
import PropTypes from "prop-types"; 
import { Link, withRouter } from "react-router-dom"; 
import { withStyles } from "@material-ui/core/styles"; 
import Grid from "@material-ui/core/Grid"; 
import Button from "@material-ui/core/Button"; 
import Typography from "@material-ui/core/Typography"; 
import TextField from "@material-ui/core/TextField"; 
import ReactExport from "react-data-export"; 
import {BootstrapTable, TableHeaderColumn} from 'react-bootstrap-table'; 
import ReactSpeedometer from "react-d3-speedometer" 
import Divider from "@material-ui/core/Divider"; 
import "./index.css"; 
import Datatable from "./table.js" 
 

const styles = theme => ({ 

 dashBoardContainer: { 
    marginTop:"150px", 
    minHeight: "80px", 
    marginLeft: "20px",
    marginRight: "20px",
    border: "none", 
    borderTop: 0, 
    boxSizing: "border-box", 
     width:"95%", 
     [theme.breakpoints.down("xs")]:{ 
      width:"100%",   
      marginLeft: "2px", 
      marginRight: "0px", 
       marginTop:"130px",  
         padding:"5px", 
         boxSizing: "border-box", 
     } 
  }, 

 

   xlsxDownloadBtn: { 
    textTransform: "capitalize", 
    backgroundColor: "#004d00", 
    borderRadius: "5px", 
    width: "20%", 
    margin: "10px 0", 
    fontSize: "12px", 
    boxShadow: "none", 
    outline: "none", 
    color: "#fff", 
    border: "none", 
    fontWeight: "bold", 
    fill: "#000", 
    padding:"10px", 
       height:"50px",
    "&:hover": { 
      backgroundColor: "#008000", 
      color: "#fff", 
        cursor: "pointer" 
    }, 

       [theme.breakpoints.down("xs")]:{ 

          width: "50%",  

       } 

  }, 

    reportHeaderDiv:{ 
        textAlign:"center", 
        height:"50px",  
        backgroundColor:"#0000e6", 
        marginBottom:"10px", 
        padding:"10px 0px 0px 0px", 
         [theme.breakpoints.down("xs")]:{ 
           height:"40px", 
paddingBottom:"8px"   
       } 
    }, 
    backClick:{width:"10%", float:"left", fontWight:"bold", border:"1px solid white", padding:"5px", margin:"5px",color:"white",
               fontSize:"20px",
               [theme.breakpoints.down("xs")]:{ 
           display:"none"
              },
               "&:hover": { 
      backgroundColor: "#008000", 
      color: "#fff", 
        cursor: "pointer" 
    }, 
              }
}); 



function DataTableComponent(props) { 
  const { classes,  history,motorData } = props;
    var tableDatalist = JSON.parse(localStorage.getItem("readdingDatalist"));
    
const ExcelFile = ReactExport.ExcelFile; 
const ExcelSheet = ReactExport.ExcelFile.ExcelSheet; 
const ExcelColumn = ReactExport.ExcelFile.ExcelColumn; 
   
 

    const rowClassNameFormat=(row, rowIdx)=> { 

  // row is whole row object 

  // rowIdx is index of row 

  return rowIdx % 2 === 0 ? 'td-column-function-even-example' : 'td-column-function-odd-example'; 

} 

    const backToDashboard=()=>{
         history.push("/Dashboard") 
    }
   

  return ( 

    <div className={classes.dashBoardContainer}> 
      <div className={classes.reportHeaderDiv} > <div className={classes.backClick} onClick={()=>backToDashboard()}>Back</div>
      <Typography style={{color:"white", fontWeight:"bold"}}>RMS - PHOTOVOLTAIC PUMP MONITOR PARAMETERS</Typography> </div>
      <div> 
      <Datatable data={tableDatalist} /> 

      </div> 

      <div style={{textAlign:"right", marginBottom:"20px"}}> 

             <ExcelFile element={<button className={classes.xlsxDownloadBtn}>Download Report</button>}> 

                <ExcelSheet data={tableDatalist} name="Employees"> 

                    <ExcelColumn label="IMEI" value="imei"/> 
                    <ExcelColumn label="DATE & TIME" value="datetime"/> 
                    <ExcelColumn label="LOCATION" value="location"/> 
                    <ExcelColumn label="SIM NUMBER"  value="simnumber" /> 
                    <ExcelColumn label="OUTPUT CURRENT" value="outputcurrent"/> 
                    <ExcelColumn label="OUTPUT VOLTAGE" value="outputvoltage"/> 
                    <ExcelColumn label="OUPUT POWER" value="ouputpower"/> 
                    <ExcelColumn label="DC BUS VOLTAGE"  value="dcbusvoltage" /> 
                    <ExcelColumn label="ALARM" value="alarm"/> 
                    <ExcelColumn label="OUTPUT FREQUENCY" value="outputfrequency"/> 
                    <ExcelColumn label="RUN STATE" value="runstate"/> 
                    <ExcelColumn label="Whater flow per min" value=""/>     

                </ExcelSheet> 

            </ExcelFile> 

</div> 

    </div> 

  ); 

} 

 

DataTableComponent.propTypes = { 

  classes: PropTypes.object.isRequired 

}; 

 

export default withStyles(styles)(withRouter(DataTableComponent)); 

 


 

 


 

 