import React, { useEffect } from "react"; 
import { connect } from "react-redux"; 
import RegisterComponent from "../../components/Registration"; 
import { setHeaderStatus, setFooterStatus,setLoginDataHeader} from "../../actions/Header"; 
import  {registration} from "../../actions/Register";
 
const Register = ({ setHeaderStatus,setFooterStatus,setLoginDataHeader,registration}) => { 
  useEffect(() => { 
    setHeaderStatus(true); 
      setFooterStatus(true); 
  }, []); 
  return <RegisterComponent setLoginDataHeader={setLoginDataHeader} registration={registration}/>; 
}; 
export default connect(null,{setFooterStatus, setHeaderStatus,setLoginDataHeader,registration})(Register); 
