import React, {useState} from 'react'; 
import { makeStyles } from '@material-ui/core/styles'; 
import Input from '@material-ui/core/Input'; 
import OutlinedInput from '@material-ui/core/OutlinedInput'; 
import FilledInput from '@material-ui/core/FilledInput'; 
import InputLabel from '@material-ui/core/InputLabel'; 
import MenuItem from '@material-ui/core/MenuItem'; 
import FormHelperText from '@material-ui/core/FormHelperText'; 
import FormControl from '@material-ui/core/FormControl'; 
import Select from '@material-ui/core/Select'; 
 
const styles = theme => ({ 
  root: { 
    display: 'flex', 
    flexWrap: 'wrap', 
  }, 
   
}); 
 
function SimpleSelect(props) { 
  const {classes}=props; 
  const [values, setValues] = React.useState({selectValue: '', name: '',}); 
 
    const {displayText}=props; 
 
  const inputLabel = React.useRef(null); 
  const [labelWidth, setLabelWidth] = React.useState(0); 
  React.useEffect(() => {setLabelWidth(inputLabel.current.offsetWidth);}, []); 
 
  function handleChange(event) {       
    props.func(event.target.value); 
    setValues(oldValues => ({...oldValues,[event.target.name]: event.target.value,})); 
  } 
     
     
  const displayValue =displayText.map(x => ( 
    <MenuItem 
      key={x.Value} 
      value={x.Name} 
      style={{ height:"10px",fontSize:"16px"}} 
    > 
      {x.Name} 
    </MenuItem> 
  ));     
 
  return ( 
    <form  autoComplete="off">      
      <FormControl variant="outlined" className={props.cssClass}> 
        <InputLabel ref={inputLabel} htmlFor="outlined-age-simple"></InputLabel> 
        <Select  
          value={values.selectValue} 
          onChange={handleChange} 
          input={<OutlinedInput labelWidth={labelWidth} name="selectValue" style={{fontSize:"16px"}}
           id="outlined-age-simple" />} 
        > 
          <MenuItem value="Select" style={{fontSize:"16px"}} > 
           Select 
          </MenuItem> 
         {displayValue} 
        </Select> 
      </FormControl> 
      
    </form> 
  ); 
} 
 
export default SimpleSelect; 
