import React, {useState} from "react"; 
import { withRouter } from "react-router-dom"; 
import { withStyles } from "@material-ui/core/styles"; 
import Grid from "@material-ui/core/Grid"; 
import Typography from "@material-ui/core/Typography"; 
import Link from "@material-ui/core/Link"; 
import ExpansionPanel from "@material-ui/core/ExpansionPanel"; 
import ExpansionPanelSummary from "@material-ui/core/ExpansionPanelSummary"; 
import ExpandMoreIcon from "@material-ui/icons/ExpandMore"; 
import TextField from "@material-ui/core/TextField"; 
import Button from "@material-ui/core/Button"; 
 

const styles = theme => ({ 
    footerMainDiv:{ 
         background: "#4d4d4d", 
          width:"100%", 
    }, 

    footerLinks:{ 
        textAlign:"center", 
        width:"25%", 
        color:"#fff",  
        borderRight:"2px solid white", 
        paddingRight:"5px",  
        paddingLeft:"5px", 
        whiteSpace:"nowrap", 
         [theme.breakpoints.down("xs")]:{ 
            fontSize:"12px", 
            fontWeight:"bold", 
              whiteSpace:"nowrap", 
        } 
    }, 
    footerLastLinks:{ 
        textAlign:"center", 
        width:"20%", 
        color:"#fff",  
        paddingRight:"5px",  
        paddingLeft:"5px", 
         [theme.breakpoints.down("xs")]:{ 
            fontSize:"12px", 
            fontWeight:"bold", 
        } 
    }, 
    footerLinkDiv:{ 
        display:"flex", 
        padding:"10px" 
    }, 
    footerText:{ 
        textAlign:"center", 
        paddingTop:"10px", 
        paddingBottom:"10px", 
        color:"#fff",  
        fontSize:"12px",  
        [theme.breakpoints.down("xs")]:{ 
           fontSize:"12px",  
        } 
    } 
}); 
 

const FooterComoponent = props => { 

  const { classes, history } = props; 

 

   

  return ( 

    <> 

   <div className={classes.footerMainDiv}> 

    
    <div className={classes.footerLinkDiv} > 

      <Link to="" className={classes.footerLinks} > 

         About 

      </Link> 

       <Link to="" className={classes.footerLinks}> 

         Contact US 

      </Link> 

       <Link to="" className={classes.footerLinks}> 

         Services 

      </Link> 

       <Link to="" className={classes.footerLastLinks}> 

         Text 

      </Link> 

      </div> 

      <div className={classes.footerText} >2019 Solar motor monitoring System.All Rights Reserved. Use of this site is subject to certain Terms Of Use. 

Need Help?, please call or text </div> 

    </div> 

 

    </> 

  ); 

}; 

 

export default withRouter(withStyles(styles)(FooterComoponent)); 
