
import React, { useEffect } from "react"; 
import HomeComponent from "../../components/Home"; 
import { connect } from "react-redux"; 
import {setHeaderStatus,setFooterStatus,setLoginDataHeader} from "../../actions/Header"; 
import {GetPumpReading} from "../../actions/Dashboard"; 
import  {login} from "../../actions/Register";
function Home({setHeaderStatus,setFooterStatus,setLoginDataHeader,GetPumpReading,login}) { 

  useEffect(() => { 
     setHeaderStatus(true); 
      setFooterStatus(true); 

  }, []); 

  return ( 

    <div> 

      <HomeComponent setLoginDataHeader={setLoginDataHeader} GetPumpReading={GetPumpReading} login={login} /> 

    </div> 

  ); 

} 

export default connect(null,{setHeaderStatus,setFooterStatus,setLoginDataHeader,GetPumpReading,login})(Home); 
