import React from "react";
import { Slider, Rail, Handles, Tracks } from "react-compound-slider";
import {Tick } from "./Tick";
import { Handle} from "./Handle";
import {Track } from "./Track";
import { styles } from "./defaultStyle";
class App extends React.Component {
constructor(props){
super(props);
this.state={
tickCount:10,
startValue:50,
endValue:100,
handleStartValue:0,
displayValue:50
}
}
handleChange = (value) => {
this.setState({
displayValue: value
})
}
render() {
let { value, tooltip, ticks } = this.state;
return (
<div className='container' >
<div style={{textAlign:"center",fontSize:"36px", fontWight:"bold"}} >{this.state.displayValue}</div>
<Slider
mode={1}
step={0.5}
domain={[this.state.startValue, this.state.endValue]}
rootStyle={styles.slider}
values={[this.state.handleStartValue]}
onChange={this.handleChange}
>
<Rail>{({ getRailProps }) => <div style={styles.rail} {...getRailProps()} />}</Rail>
<Handles>
{({ handles, getHandleProps }) => (
<div className="slider-handles">
{handles.map(handle => (
<Handle
key={handle.id}
handle={handle}
domain={[this.state.startValue, this.state.endValue]}
getHandleProps={getHandleProps}
/>
))}
</div>
)}
</Handles>
<Tracks right={false}>
{({ tracks, getTrackProps }) => (
<div className="slider-tracks">
{tracks.map(({ id, source, target }) => (
<Track
key={id}
source={source}
target={target}
getTrackProps={getTrackProps}
/>
))}
</div>
)}
</Tracks>
{this.state.tickCount !== 0 ? (
<Slider.Ticks count={this.state.tickCount}>
{({ ticks }) => (
<div>
{ticks.map(tick => (
<Tick key={tick.id} tick={tick} count={ticks.length} />
))}
</div>
)}
</Slider.Ticks>
) : (
<div />
)}
</Slider>
</div>
);
}
}
export default App;
