
import React from "react"; 

import DataTableComponent from "../../components/DataTable"; 

import { connect } from "react-redux"; 

 

const DataTable = ({motorData}) => { 

  return ( 
    <div> 
      <DataTableComponent motorData={motorData} /> 
    </div> 

  ); 

}; 
export default connect(({ datatable }) => ({motorData:datatable.motorData}),{ })(DataTable); 
