
import React, { useState } from "react"; 
import PropTypes from "prop-types"; 
import { Link, withRouter } from "react-router-dom"; 
import { withStyles } from "@material-ui/core/styles"; 
import Grid from "@material-ui/core/Grid"; 
import Button from "@material-ui/core/Button"; 
import Typography from "@material-ui/core/Typography"; 
import TextField from "@material-ui/core/TextField"; 
import CardContent from "@material-ui/core/CardContent"; 
import Card from "@material-ui/core/Card"; 
import { light } from "@material-ui/core/styles/createPalette"; 
import { gray } from "ansi-colors"; 
import NumberFormat from "react-number-format"; 
 import DropDown from '../Helpers/SelectDropDown'; 
const styles = theme => ({ 

  root: { 
    display: "flex", 
    position: "relative", 
    top: "50%", 
    left: "30%",
    transform: "translate(-50%, -50%)", 
    height: "100%", 
    width: "100%", 
    flexDirection: "column", 
    marginTop:"570px",
       [theme.breakpoints.down("sm")]:
      { 
          marginTop:"480px", 
            width: "100%", 
          left: "35%",

      } ,
    [theme.breakpoints.down("xs")]:
      { 
          marginTop:"290px", 
          //marginBottom:"80px",
            width: "100%", 
          height: "300px", 
           left: "50%",
         top: "50%", 
      } 

  }, 

  lblStyle: { 

    margin: "20px 0px", 

    textAlign: "left" 

  }, 

  txtStyle: { 
    width: "100%",
    height: "50px",
    marginBottom:"20px",
      
  }, 

  btnStyle: { 
    textTransform: "capitalize", 
    backgroundColor: "#004d00", 
    borderRadius: "5px", 
    width: "100%", 
    margin: "10px 0", 
    fontSize: "12px", 
    boxShadow: "none", 
    outline: "none", 
    color: "#fff", 
    border: "none", 
    fontWeight: "bold", 
    fill: "#000",
      height:"50px",
    "&:hover": { 
      backgroundColor: "#008000", 
      color: "#fff" 
    } 
  }, 

  width100: { 

    width: "100%" 

  }, 

  reghide: { 

    [theme.breakpoints.down("sm")]: { 

      display: "none" 

    } 

  }, 

  rowHeight: { 

    height: "40px" 

  }, 

  rowHeight30: { 

    height: "50px" 

  }, 

  loginHead: { 

    textAlign: "center", 

    fontSize: "24px" 

  }, 

  logo: { 

    width: "100px", 

    height: "40px", 

    display: "block", 

    margin: "0 auto", 

    padding: "50px 0 0 0" 

  }, 

  register: { 

    fontSize: "14px", 

    textAlign: "center" 

  }, 

  spinnerDiv: { 

    textAlign: "center", 

    marginTop: "20%" 

  }, 

  forgotPwd: { 
    textAlign: "right" 
  }, 

    loginHeader:{ 
    color:"#004d00", 
    fontSize:"26px",  
    margin:"15px 0px 15px  0px", 
    textAlign:"center" 
}, 

     InputText: { 
    width: "100%",
    height: "35px", 
    marginTop: "0px", 
    margin: "0px 0px 10px 0px" 
  },
width98:{
    width:"100%"
},
regRightDiv:{
     width:"96%",
    paddingLeft:"15px",
     [theme.breakpoints.down("sm")]: {
       width:"100%",
           paddingLeft:"0px",
    } 
},
    selectDropDown: { 
    width: "100%", 
    height: "50px", 
    marginBottom:"20px", 
    fontSize:"16px", 
    [theme.breakpoints.down("xs")]: { 
       width: "100%", 
       marginBottom:"20px" ,
            height: "50px", 

    } 
  },
    errorClass:{
        color:"red", 
        textAlign:"center"
    }
}); 

 

 

 

 

 

function RegisterComponent(props) { 

  const { classes,  history, styleFromHomepage, setLoginDataHeader,registration } = props; 

     

    const [userName, setUserName] = useState(""); 
    const handleUsername=(value)=>{ 
        setUserName(value); 
    } 
    
    const [firstName, setFirstName] = useState(""); 
    const handleFirstName=(value)=>{ 
        setFirstName(value); 
    } 
    
    const [lastName, setLastName] = useState(""); 
    const handleLastName=(value)=>{ 
        setLastName(value); 
    } 
    
    const [farmerPhoneNumber, setFarmerPhoneNumber] = useState(""); 
    const handleFarmerPhoneNumber=(value)=>{ 
        setFarmerPhoneNumber(value); 
    }
    const [RMSPhoneNumber, setRMSPhoneNumber] = useState(""); 
    const handleRMSPhoneNumber=(value)=>{ 
        setRMSPhoneNumber(value); 
    }
    const [moterType, setMoterType] = useState(""); 
    const handleMoterType=(value)=>{ 
        setMoterType(value); 
    }
    
     const [moterHP, setMoterHP] = useState(""); 
    const handleMoterHP=(value)=>{ 
        setMoterHP(value); 
    }
    const [moter, setMoter] = useState(""); 
    const handleMoter=(value)=>{
        setMoter(value); 
    }
    
     const [landAddress, setLandAddress] = useState(""); 
    const handleLandAddress=(value)=>{ 
        setLandAddress(value); 
    }
      const [farmerAddress, setFarmerAddress] = useState(""); 
    const handleFarmerAddress=(value)=>{ 
        setFarmerAddress(value); 
    }
    const [password, setPassword] = useState(""); 
    const handlePassword=(value)=>{ 
        setPassword(value); 
    }
    const [confirmPassword, setConfirmPassword] = useState(""); 
    const handleConfirmPassword=(value)=>{ 
        setConfirmPassword(value); 
    }
    
   const [locationLat, setLocationLat] = useState(""); 
    const handleLocationLat=(value)=>{ 
        setLocationLat(value); 
    }
    const [locationLong, setLocationLong] = useState(""); 
    const handleLocationLongt=(value)=>{ 
        setLocationLong(value); 
    }
    const [imeiNumber, setImeiNumber] = useState(""); 
    const handleIMEI=(value)=>{ 
        setImeiNumber(value); 
    }
    
    
     const [errorRegister, setErrorRegister] = useState(""); 
    
    
     const handleSubmit = e => {
    e.preventDefault();
         registration({
             Imei: imeiNumber,
             FirstName: firstName,
             LastName: lastName,
             UserName: userName,
             FarmerPhNo: farmerPhoneNumber,
             RMSPhNo: RMSPhoneNumber,
             MotorType: moterType,
             MotorHP: moterHP,
             MotorCurrentType: moter,
             Latitude: locationLat,
             Longitude: locationLong,
             LandAddress: landAddress,
             FarmerAddress: farmerAddress,
             Password: password,
             ConfirmPassword: confirmPassword,
        
      }).then(resp => navigateToHome(resp));    
  };
const navigateToHome = data => {
    let result=data?data.payload.status:"";
    console.log("resp register", result);
    if(result=="success"){
        history.push("/Login")
    }
    else{
       setErrorRegister("Error while registration");
    }
    //const userData = data.payload;
     //history.push("/Login")
  };

const inputStyle = { WebkitBoxShadow: "0 0 0 1000px white inset" }; 

    
    
    const MoterTypes = [ 
        { Name: "Fuchi",  Value:"1" }, 
        { Name: "Vichi", Value:"2" }, 
        { Name: "Cichi",  Value:"3" }, 
             ];
     const Moters = [ 
        { Name: "AC",  Value:"1" }, 
        { Name: "DC", Value:"2" }, 
             ];
  return ( 

    <div className={styleFromHomepage?styleFromHomepage:classes.root}> 

     <div><Typography className={classes.loginHeader} ><strong>FARMER DETAILS</strong></Typography></div> 

        <Grid container> 

            <Grid lg={4} sm={4} item /> 
            <Grid item lg={8} sm={8} xs={12}> 
              <Card> 
                <CardContent> 
               <form onSubmit={handleSubmit} >   
      <Grid container>
     <Grid item lg={6} sm={6} xs={12} >
      <Grid item lg={12} sm={10} xs={12} >
            <div className={classes.width98}><Typography style={{marginBottom:"-10px"}}> First Name </Typography> 
      
                <TextField 

                          placeholder="Enter first name" 
                          margin="normal" 
                          variant="outlined"
                          className={classes.txtStyle}
                          value="" 
                          value={firstName}
                            inputProps={{ style: inputStyle }} 
                          onChange={e => handleFirstName(e.target.value)}
                /> 
      </div>     
      </Grid>
<Grid item lg={12} sm={10} xs={12} >
            <div className={classes.width98}><Typography style={{marginBottom:"-10px"}}> Last Name </Typography> 
      
                <TextField 

                          placeholder="Enter last name" 
                          margin="normal" 
                          variant="outlined"
                          className={classes.txtStyle}
                          value="" 
                          value={lastName}
                            inputProps={{ style: inputStyle }} 
                          onChange={e => handleLastName(e.target.value)}
                /> 
      </div>     
      </Grid>
    <Grid item lg={12} sm={10} xs={12} >
            <div className={classes.width98}><Typography style={{marginBottom:"-10px"}}>Farmer Phone</Typography> 
      <TextField 
                          placeholder="Enter farmer phone number" 
                          margin="normal" 
                          variant="outlined" 
                          className={classes.txtStyle} 
                          value={farmerPhoneNumber} 
                            inputProps={{ style: inputStyle }} 
                          onChange={e => handleFarmerPhoneNumber(e.target.value)} 
                        />  
      </div>     
      </Grid>
   <Grid item lg={12} sm={10} xs={12} >
            <div className={classes.width98}><Typography style={{marginBottom:"-10px"}}> RMS Phone</Typography> 
      
                     <TextField 
                          placeholder="Enter RMS  phone number" 
                          margin="normal" 
                          variant="outlined" 
                          className={classes.txtStyle} 
                          value="" 
                          value={RMSPhoneNumber} 
                            inputProps={{ style: inputStyle }} 
                          onChange={e => handleRMSPhoneNumber(e.target.value)} 
                        /> 
      </div>     
      </Grid>
<Grid item lg={12} sm={10} xs={12} >
            <div className={classes.width98}><Typography style={{marginBottom:"-10px"}}> IMEI Number</Typography> 
      
                     <TextField 
                          placeholder="Enter IMEI  number" 
                          margin="normal" 
                          variant="outlined" 
                          className={classes.txtStyle} 
                          value="" 
                          value={imeiNumber} 
                            inputProps={{ style: inputStyle }} 
                          onChange={e => handleIMEI(e.target.value)} 
                        /> 
      </div>     
      </Grid>
 <Grid item lg={12} sm={10} xs={12} >
            <div className={classes.width98}><Typography style={{marginBottom:"5px"}}> Moter Type </Typography> 
      
                    <DropDown cssClass={classes.selectDropDown} func={handleMoterType} displayText={MoterTypes} /> 
      </div>     
      </Grid>
  <Grid item lg={12} sm={10} xs={12} >
            <div className={classes.width98}><Typography style={{marginBottom:"5px"}}> Moter </Typography> 
      
                    <DropDown cssClass={classes.selectDropDown} func={handleMoter} displayText={Moters} /> 
      </div>     
      </Grid>

 
      </Grid>
      <Grid item lg={6} sm={6} xs={12} >
      
          
    <Grid item lg={12} sm={10} xs={12} >
            <div className={classes.regRightDiv}><Typography style={{marginBottom:"-10px"}}>Moter HP </Typography> 
      <TextField 
                          placeholder="Enter moter HP" 
                          margin="normal" 
                          variant="outlined" 
                          className={classes.txtStyle} 
                          value="" 
                          value={moterHP} 
                            inputProps={{ style: inputStyle }} 
                          onChange={e => handleMoterHP(e.target.value)} 
                        />  
      </div>     
      </Grid>
  
 <Grid item lg={12} sm={10} xs={12} >
            <div className={classes.regRightDiv}><Typography style={{marginBottom:"-10px"}}> Land Address </Typography> 
      
                     <TextField 
                          placeholder="Enter land address" 
                          margin="normal" 
                          variant="outlined" 
                          className={classes.txtStyle} 
                          value="" 
                          value={landAddress} 
                            inputProps={{ style: inputStyle }} 
                          onChange={e => handleLandAddress(e.target.value)} 
                        /> 
      </div>     
      </Grid>
 <Grid item lg={12} sm={10} xs={12} >
            <div className={classes.regRightDiv}><Typography style={{marginBottom:"-10px"}}>  Farmer Address </Typography> 
      
                     <TextField 
                          placeholder="Enter address" 
                          margin="normal" 
                          variant="outlined" 
                          className={classes.txtStyle} 
                          value="" 
                          value={farmerAddress} 
                            inputProps={{ style: inputStyle }} 
                          onChange={e => handleFarmerAddress(e.target.value)} 
                        /> 
      </div>     
      </Grid>

 <Grid item lg={12} sm={10} xs={12} >
            <div className={classes.regRightDiv}><Typography style={{marginBottom:"-10px"}}> User Name </Typography> 
      
                     <TextField 
                          placeholder="Enter username" 
                          margin="normal" 
                          variant="outlined" 
                          className={classes.txtStyle} 
                          value="" 
                          value={userName} 
                            inputProps={{ style: inputStyle }} 
                          onChange={e => handleUsername(e.target.value)} 
                        /> 
      </div>     
      </Grid> <Grid item lg={12} sm={10} xs={12} >
            <div className={classes.regRightDiv}><Typography style={{marginBottom:"-10px"}}> Password </Typography> 
      
                     <TextField 
                          placeholder="Enter password" 
                          margin="normal" 
                          variant="outlined" 
                          className={classes.txtStyle} 
                          value="" 
                          value={password} 
                            inputProps={{ style: inputStyle }} 
                          onChange={e => handlePassword(e.target.value)} 
                        /> 
      </div>     
      </Grid> 
<Grid item lg={12} sm={10} xs={12} >
            <div className={classes.regRightDiv}><Typography style={{marginBottom:"-10px"}}>Confirm Password </Typography> 
      
                     <TextField 
                          placeholder="Re-Enter password" 
                          margin="normal" 
                          variant="outlined" 
                          className={classes.txtStyle} 
                          value="" 
                          value={confirmPassword} 
                            inputProps={{ style: inputStyle }} 
                          onChange={e => handleConfirmPassword(e.target.value)} 
                        /> 
      </div>     
      </Grid>
<Grid item lg={12} sm={10} xs={12} >
    
  
            <div className={classes.regRightDiv}><Typography style={{marginBottom:"-10px"}}>Location</Typography> 
        <Grid container>
<Grid item lg={6} sm={6} xs={6} style={{paddingRight:"5px"}} >
    <TextField 
                          placeholder="Enter lat" 
                          margin="normal" 
                          variant="outlined" 
                          className={classes.txtStyle} 
                          value="" 
                          value={locationLat} 
                            inputProps={{ style: inputStyle }} 
                          onChange={e => handleLocationLat(e.target.value)} 
                        /> 
    
    </Grid>
    <Grid item lg={6} sm={6} xs={6} style={{paddingLeft:"5px"}} >
        
        
        <TextField 
                          placeholder="Enter long" 
                          margin="normal" 
                          variant="outlined" 
                          className={classes.txtStyle} 
                          value="" 
                          value={locationLong} 
                            inputProps={{ style: inputStyle }} 
                          onChange={e => handleLocationLongt(e.target.value)} 
                        /> 
        </Grid>    
        </Grid>
                     
      </div>     
      </Grid>
      </Grid>
      </Grid>
                 <Grid container>
                     
 <Grid item lg={12} sm={10} xs={12} >
            <div><Button className={classes.btnStyle} type="submit"> REGISTER</Button>
      </div>     
      </Grid>
      <Grid item lg={12} sm={10} xs={12} style={{marginBottom:"20px"}} >
            <div className={classes.errorClass} >{errorRegister?errorRegister:""}</div>
         
      </Grid>
      </Grid>


                  


                   

                  

                  
                    

                  </form> 

                </CardContent> 

              </Card> 

            </Grid> 

          </Grid> 

    </div> 

  ); 

} 

 

RegisterComponent.propTypes = { 

  classes: PropTypes.object.isRequired 

}; 

 

export default withStyles(styles)(withRouter(RegisterComponent)); 
